<template>
  <div class="pdf-convert">
    <el-card class="box-card">
      <template #header>
        <div class="card-header">
          <span>PDF Format Conversion</span>
          <el-icon><Refresh /></el-icon>
        </div>
      </template>
      
      <el-form :model="form" label-width="120px">
        <el-form-item label="Select PDF File" required>
          <el-upload
            ref="uploadRef"
            :auto-upload="false"
            :limit="1"
            :on-change="handleFileChange"
            :before-upload="beforeUpload"
            accept=".pdf"
            drag
          >
            <el-icon class="el-icon--upload"><upload-filled /></el-icon>
            <div class="el-upload__text">
              Drag PDF file here, or <em>click to upload</em>
            </div>
            <template #tip>
              <div class="el-upload__tip">
                Only PDF files allowed, max 100MB
              </div>
            </template>
          </el-upload>
        </el-form-item>

        <el-form-item label="Convert Type" required>
          <el-radio-group v-model="form.convertType" @change="handleConvertTypeChange">
            <el-radio label="word">To Word</el-radio>
            <el-radio label="excel">To Excel</el-radio>
            <el-radio label="ppt">To PPT</el-radio>
            <el-radio label="image">To Images</el-radio>
            <el-radio label="txt">To TXT</el-radio>
            <el-radio label="html">To HTML</el-radio>
            <el-radio label="markdown">To Markdown</el-radio>
          </el-radio-group>
        </el-form-item>

        <!-- Word conversion options -->
        <template v-if="form.convertType === 'word'">
          <el-form-item label="Output Format">
            <el-radio-group v-model="form.outputFormat">
              <el-radio label="docx">DOCX</el-radio>
              <el-radio label="doc">DOC</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="Preserve Formatting">
            <el-switch v-model="form.preserveFormatting" />
            <div class="form-tip">Whether to preserve original PDF formatting</div>
          </el-form-item>
        </template>

        <!-- Excel conversion options -->
        <template v-if="form.convertType === 'excel'">
          <el-form-item label="Output Format">
            <el-radio-group v-model="form.outputFormat">
              <el-radio label="xlsx">XLSX</el-radio>
              <el-radio label="xls">XLS</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="Extract Tables Only">
            <el-switch v-model="form.extractTables" />
            <div class="form-tip">Whether to extract only table content from PDF</div>
          </el-form-item>
        </template>

        <!-- PPT conversion options -->
        <template v-if="form.convertType === 'ppt'">
          <el-form-item label="Output Format">
            <el-radio-group v-model="form.outputFormat">
              <el-radio label="pptx">PPTX</el-radio>
              <el-radio label="ppt">PPT</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="One Page Per Slide">
            <el-switch v-model="form.onePagePerSlide" />
            <div class="form-tip">Whether to convert each PDF page to one slide</div>
          </el-form-item>
        </template>

        <!-- Image conversion options -->
        <template v-if="form.convertType === 'image'">
          <el-form-item label="Image Format">
            <el-radio-group v-model="form.imageFormat">
              <el-radio label="png">PNG</el-radio>
              <el-radio label="jpg">JPG</el-radio>
              <el-radio label="jpeg">JPEG</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="Resolution (DPI)">
            <el-input-number
              v-model="form.dpi"
              :min="72"
              :max="600"
              :step="72"
            />
          </el-form-item>
          <el-form-item label="Page Range">
            <el-input
              v-model="form.pageRange"
              placeholder="e.g: 1-5 (leave empty to convert all)"
              clearable
            />
            <div class="form-tip">Specify page range to convert, leave empty to convert all pages</div>
          </el-form-item>
        </template>

        <!-- TXT conversion options -->
        <template v-if="form.convertType === 'txt'">
          <el-form-item label="Text Encoding">
            <el-select v-model="form.encoding" placeholder="Select encoding">
              <el-option label="UTF-8" value="utf-8" />
              <el-option label="GBK" value="gbk" />
              <el-option label="GB2312" value="gb2312" />
              <el-option label="ISO-8859-1" value="iso-8859-1" />
            </el-select>
          </el-form-item>
          <el-form-item label="Page Range">
            <el-input
              v-model="form.pageRange"
              placeholder="e.g: 1-5 (leave empty to convert all)"
              clearable
            />
          </el-form-item>
        </template>

        <!-- HTML conversion options -->
        <template v-if="form.convertType === 'html'">
          <el-form-item label="Split by Page">
            <el-switch v-model="form.splitByPage" />
          </el-form-item>
          <el-form-item label="Embed Images">
            <el-switch v-model="form.embedImages" />
          </el-form-item>
          <el-form-item label="Page Range">
            <el-input
              v-model="form.pageRange"
              placeholder="e.g: 1-5 (leave empty to convert all)"
              clearable
            />
          </el-form-item>
        </template>

        <!-- Markdown conversion options -->
        <template v-if="form.convertType === 'markdown'">
          <el-form-item label="Heading Detection">
            <el-select v-model="form.headingDetection">
              <el-option label="Auto" value="auto" />
              <el-option label="Simple" value="simple" />
              <el-option label="None" value="none" />
            </el-select>
          </el-form-item>
          <el-form-item label="Include Images">
            <el-switch v-model="form.includeImages" />
          </el-form-item>
          <el-form-item label="Page Range">
            <el-input
              v-model="form.pageRange"
              placeholder="e.g: 1-5 (leave empty to convert all)"
              clearable
            />
          </el-form-item>
        </template>

        <el-form-item>
          <el-button
            type="primary"
            :loading="loading"
            :disabled="!selectedFile"
            @click="handleSubmit"
          >
            {{ loading ? 'Converting...' : 'Start Convert' }}
          </el-button>
          <el-button @click="resetForm">Reset</el-button>
        </el-form-item>
      </el-form>

      <!-- Results Display -->
      <div v-if="result" class="result-section">
        <el-divider>Conversion Results</el-divider>
        <el-alert
          :title="result.msg"
          type="success"
          :closable="false"
          show-icon
        />
        <div class="result-info">
          <p><strong>Task ID:</strong> {{ result.data.taskId }}</p>
          <p v-if="form.convertType === 'image'">
            <strong>Converted Images:</strong> {{ result.data.images?.length || 0 }}
          </p>
          <p v-else>
            <strong>Converted File:</strong> {{ result.data.convertedFile }}
          </p>
          <el-button type="success" @click="downloadResult">
            <el-icon><Download /></el-icon>
            Download Result
          </el-button>
        </div>
      </div>
    </el-card>
  </div>
</template>

<script setup>
import { ref, reactive } from 'vue'
import { ElMessage } from 'element-plus'
import { Refresh, UploadFilled, Download } from '@element-plus/icons-vue'
import { pdfApi } from '@/api'
import { validateFile, FILE_TYPES } from '@/utils/file'

const uploadRef = ref()
const loading = ref(false)
const selectedFile = ref(null)
const result = ref(null)

const form = reactive({
  convertType: 'word',
  outputFormat: 'docx',
  preserveFormatting: true,
  extractTables: false,
  onePagePerSlide: true,
  imageFormat: 'png',
  dpi: 300,
  pageRange: '',
  encoding: 'utf-8',
  splitByPage: true,
  embedImages: true,
  headingDetection: 'auto',
  includeImages: true
})

// Convert type change handling
const handleConvertTypeChange = () => {
  // Reset related options
  if (form.convertType === 'word') {
    form.outputFormat = 'docx'
  } else if (form.convertType === 'excel') {
    form.outputFormat = 'xlsx'
  } else if (form.convertType === 'ppt') {
    form.outputFormat = 'pptx'
  } else if (form.convertType === 'image') {
    form.imageFormat = 'png'
    form.dpi = 300
    form.pageRange = ''
  } else if (form.convertType === 'txt') {
    form.encoding = 'utf-8'
    form.pageRange = ''
  } else if (form.convertType === 'html') {
    form.splitByPage = true
    form.embedImages = true
    form.pageRange = ''
  } else if (form.convertType === 'markdown') {
    form.headingDetection = 'auto'
    form.includeImages = true
    form.pageRange = ''
  }
}

// File selection handling
const handleFileChange = (file) => {
  const validation = validateFile(file.raw, 'PDF')
  if (!validation.valid) {
    ElMessage.error(validation.errors[0])
    uploadRef.value.clearFiles()
    return
  }
  selectedFile.value = file.raw
}

// Pre-upload validation
const beforeUpload = (file) => {
  const validation = validateFile(file, 'PDF')
  if (!validation.valid) {
    ElMessage.error(validation.errors[0])
    return false
  }
  return false // Prevent auto-upload
}

// Submit processing
const handleSubmit = async () => {
  if (!selectedFile.value) {
    ElMessage.warning('Please select a PDF file first')
    return
  }

  loading.value = true
  try {
    const formData = new FormData()
    formData.append('file', selectedFile.value)

    let response
    switch (form.convertType) {
      case 'word':
        formData.append('outputFormat', form.outputFormat)
        formData.append('preserveFormatting', form.preserveFormatting.toString())
        response = await pdfApi.convertToWord(formData)
        break
      case 'excel':
        formData.append('outputFormat', form.outputFormat)
        formData.append('extractTables', form.extractTables.toString())
        response = await pdfApi.convertToExcel(formData)
        break
      case 'ppt':
        formData.append('outputFormat', form.outputFormat)
        formData.append('onePagePerSlide', form.onePagePerSlide.toString())
        response = await pdfApi.convertToPpt(formData)
        break
      case 'image':
        formData.append('imageFormat', form.imageFormat)
        formData.append('dpi', form.dpi.toString())
        formData.append('pageRange', form.pageRange)
        response = await pdfApi.convertToImages(formData)
        break
      case 'txt':
        formData.append('encoding', form.encoding)
        formData.append('pageRange', form.pageRange)
        response = await pdfApi.convertToTxt(formData)
        break
      case 'html':
        formData.append('splitByPage', form.splitByPage.toString())
        formData.append('embedImages', form.embedImages.toString())
        formData.append('pageRange', form.pageRange)
        response = await pdfApi.convertToHtml(formData)
        break
      case 'markdown':
        formData.append('includeImages', form.includeImages.toString())
        formData.append('headingDetection', form.headingDetection)
        formData.append('pageRange', form.pageRange)
        response = await pdfApi.convertToMarkdown(formData)
        break
    }

    result.value = response
    ElMessage.success('PDF conversion successful')
  } catch (error) {
    console.error('PDF conversion failed:', error)
  } finally {
    loading.value = false
  }
}

// Download result
const downloadResult = async () => {
  if (result.value?.data?.downloadUrl) {
    try {
      const { downloadFile } = require('@/utils/download')
      const filename = form.convertType === 'image' ? 'converted_images.zip' : result.value.data.convertedFile
      downloadFile(result.value.data.downloadUrl, filename)
      ElMessage.success('Download started')
    } catch (error) {
      console.error('Download failed:', error)
      ElMessage.error('Download failed, please try again')
    }
  } else {
    ElMessage.warning('No file available for download')
  }
}

// Reset form
const resetForm = () => {
  form.convertType = 'word'
  form.outputFormat = 'docx'
  form.preserveFormatting = true
  form.extractTables = false
  form.onePagePerSlide = true
  form.imageFormat = 'png'
  form.dpi = 300
  form.pageRange = ''
  selectedFile.value = null
  result.value = null
  uploadRef.value?.clearFiles()
}
</script>

<style scoped>
.pdf-convert {
  max-width: 800px;
  margin: 0 auto;
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 18px;
  font-weight: bold;
}

.form-tip {
  font-size: 12px;
  color: #909399;
  margin-top: 5px;
}

.result-section {
  margin-top: 20px;
}

.result-info {
  margin-top: 15px;
  padding: 15px;
  background-color: #f5f7fa;
  border-radius: 4px;
}

.result-info p {
  margin: 8px 0;
}
</style>
